html, body {
    margin: 0;
    padding: 0;
    color: #f4f4f4;
    height: 100%;
    min-height: 100%;
    overflow: overlay;
    min-width: 1350px;
    background: transparent;
    font-family: "Roboto", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

body {
    background: url("img/background.jpg") no-repeat 50% 50%;
    background-size: cover;
}

.main {
    padding: 0 2rem;
}

.container {
    min-width: calc(1350px - 2rem) !important;
}

.container.tab-content {
    min-width: calc(1350px - 4rem) !important;
}

.col-3 {
    max-width: 20%;
}

a {
    color: #257cda;
}

.input-group {
    margin-bottom: .5rem;
}

.input-group > .form-control:not(:first-child), .input-group > .custom-select:not(:first-child) {
    border-radius: .2rem;
}
.form-control:focus {
    outline: 0;
    box-shadow: none;
    border: 1px solid #ced4da;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.canvas-container {
    border-radius: .25rem;
    position: relative;
    min-width: 1003px;
    min-height: 623px;
}

.canvas-container.loading:before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background: #2b2b2b;
    z-index: 100;
}

.canvas-container.loading:after {
    content: "";
    position: absolute;
    display: inline-block;
    width: 80px;
    height: 80px;
    background: url(img/bookup_loading.gif) no-repeat 50% 50%;
    background-size: 100% 100%;
    top: 50%;
    margin-top: -40px;
    left: 50%;
    margin-left: -40px;
    z-index: 101;
}

.elements {
    height: 100%;
}

.color-property {
    min-width: 70px;
    max-width: 100px;
}

.nopadding {
    padding: 0 !important;
    margin: 0 !important;
}

.header {
    padding: .5rem 1rem;
    display: flex;
    justify-content: start;
    align-items: center;
    background: #2b2b2b;
    margin-bottom: 1rem;
    box-shadow: 0 0 10px #000;
}
.header h1 {
    display: inline-flex;
    font-size: 20px;
    padding: 0 0 0 20px;
}
.header .logo {
    display: inline-flex;
    width: 64px;
    height: 64px;
    background: url("img/logo.svg") no-repeat 50% 50%;
    background-size: 150px 150px;
}

/* Tabs */
.nav {
    padding-left: 1rem;
}

.nav-tabs {
    border-bottom: 0;
}

.nav-tabs .nav-item {
    margin-bottom: -2px;
}

.nav-link {
    display: block;
    padding: .8rem 2rem;
    background: #161616;
    color: #FFFFFF;
}
.nav-tabs .nav-link.active, .nav-tabs .nav-item.show .nav-link {
    color: #161616 !important;
    background-color: #ffffff;
    border-color: #000000;
}
.nav-tabs .nav-link {
    border: 2px solid #000000;
}

.nav-tabs .nav-link:hover {
    color: #ffffff;
    border-color: #000000;
    transition: .3s ease all;
}

.tab-content {
    background: #2b2b2b;
    padding: 1rem;
    margin: auto;
    border-radius: .2rem;
    width: calc(100% - 2rem);
    box-shadow: 0 0 10px #000;
}

/* accordion*/

.accordion > .card {
    background: #f4f4f4;
    border-radius: .25rem;
    border: 0;
    margin-bottom: .2rem;
    overflow: visible;
}

.accordion .card-body {
    padding: .8rem 1.25rem;
    background: #ececec;
    border-radius: 0 0 .25rem .25rem;
}

.accordion .card .card-header {
    padding: 0;
    border-bottom: 0;
}

.accordion .btn {
    display: block;
    color: #080808;
    padding: .5rem 1rem;
    border-radius: 0;
}

.accordion .btn:not(.collapsed) {
    font-weight: bold;
}

.accordion .btn:focus {
    box-shadow: none;
}

.card-header:first-child {
    border-radius: 0;
}

.accordion .btn .oi:empty:before {
    font-size: .6rem;
}

.accordion .btn.collapsed .oi {
    transform: rotate(0deg);
    top: 1px;
}

.accordion .btn .oi {
    transform: rotate(180deg);
    top: 5px;
}

/* color-pickers */

.accordion .card .color-picker .btn {
    padding: 0;
    width: 1.7rem;
    border: 1px solid #ced4da;
    border-radius: .2rem;
    margin-right: .3rem;
    box-shadow: 0px 0px 1px rgba(3, 3, 3, 0.5);
}

.accordion .card .color-picker .form-control {
    margin-right: .3rem;
    height: calc(1.4em + 0.3rem + 2px);
    padding: 0.2rem 0.4rem;;
}

#editor {
    position: absolute;
    top: 57px;
    left: 377px;
    width: 589px;
    height: 523px;
}

.container.mw-80 {
    max-width: 80%;
    min-width: 1350px
}

@media (min-width: 1200px) {
    .container {
        max-width: 100%;
    }
}
